// May 22, 2024
function getCurrentFormattedDate1() {
    const date = new Date();
    const options = { year: 'numeric', month: 'long', day: 'numeric' };
    return date.toLocaleDateString('en-US', options);
}

// 2024, May 22
function getCurrentFormattedDate2() {
    const date = new Date();
    const options = { year: 'numeric', month: 'long', day: 'numeric' };
    const formattedDate = date.toLocaleDateString('en-US', options);
    
    const [monthDay, year] = formattedDate.split(', ');
    return `${year}, ${monthDay}`;
}

// 17 May 2023
function getCurrentFormattedDate3() {
    const date = new Date();
    const options = { day: 'numeric', month: 'long', year: 'numeric' };
    return date.toLocaleDateString('en-GB', options);
}

// 2024 
function getCurrentYear() {
    const date = new Date();
    return date.getFullYear().toString();
}



export { getCurrentFormattedDate1, getCurrentFormattedDate2, getCurrentFormattedDate3, getCurrentYear };