import React, { useState, useEffect } from 'react';
import { TypeAnimation } from 'react-type-animation';

import Title     from "./Components/Title";
import Excerpt   from "./Components/Excerpt";
import Result    from "./Components/Result";
import Share     from "./Components/Share";
import Preloader from "./Components/Preloader";
import jsPDF     from 'jspdf';
import html2canvas from 'html2canvas';


import { getCurrentFormattedDate1, getCurrentFormattedDate2, getCurrentFormattedDate3, getCurrentYear }  from './helpers';

import './Components/Switcher.css';
import './Components/functionalButtons.css';

const App = () => {
  const [ inputText,        setInputText ]        = useState('');
  const [ outputText,       setOutputText ]       = useState('');

  const [ inputExcerpt,     setInputExcerpt ]     = useState('');
  const [ outputExcerpt,    setOutputExcerpt ]    = useState('');

  const [ selectedOption,   setSelectedOption ]   = useState('book');
  const [ showNote,         setShowNote ]         = useState(false);
  const [ showNoteTitle,    setShowNoteTitle ]    = useState(false);
  const [ showTitle,        setShowTitle ]        = useState(true);
  const [ showExcerpt,      setShowExcerpt ]      = useState(false);
  const [ isLoading,        setIsLoading ]        = useState(false);
  const [ requestCancelled, setRequestCancelled ] = useState(false);
  const [ isError,          setIsError ]          = useState(false);
  const [ isCiteTitle,      setIsCiteTitle ]      = useState(false);
  const [ isCiteExcerpt,    setIsCiteExcerpt ]    = useState(false);
  const [ quoteTitle,       setQuoteTitle ]       = useState(null);
  const [ quoteExcerpt,     setQuoteExcerpt ]     = useState(null);


  const apiKey = process.env.REACT_APP_OPENAI_API_KEY;
  const apiUrl = "https://api.openai.com/v1/chat/completions";

  // Object to map option values to their corresponding placeholders
  const placeholders = {
    show:  'Choose a Book, Poem, Movie, or Song...',
    book:  'Enter Book title...',
    poem:  'Enter Poem title...',
    movie: 'Enter Movie title...',
    song:  'Enter Song title...',
};

// Function to count words in a string
function wordCount(str) {
  return str.trim().split(/\s+/).length;
}

  const titleExcerptToggle = () => {
    setShowTitle(value => !value);
    setShowExcerpt(value => !value);
  }

  const showPopup = (message) => {
    // Show the popup with the provided message
    const popup = document.getElementById('popup');
    popup.textContent = message;
    popup.style.display = 'block';
    // Hide the popup after 2 seconds
    setTimeout(() => {
      popup.style.display = 'none';
    }, 3000);
  };

  // HTML markup for Output
  function convertLinefeedsToHTML(text) {
    // return text.replace(/\n/g, '<br>');
    return text;
  }

  //   return aiResponse.replace('<br>', /\n/g);
  function convertAIresponseToText(aiResponse) {
    return aiResponse
        // Replace <br> with newline
        .replace(/<br\s*\/?>/gi, '\n')
        // Replace <ul> with newline (optional, if you want an extra line before the list)
        // .replace(/<ul>/gi, '\n')
        // Replace <li> with newline
        .replace(/<li>/gi, '\n- ')
        // Remove closing tags
        .replace(/<\/ul>|<\/li>|<\/b>|<\/i>|<\/strong>|<\/em>/gi, '')
        // Remove remaining HTML tags
        .replace(/<[^>]+>/g, '');
}

  const handleSubmit = async (e) => {
    setShowNote(false);
    setShowNoteTitle(false);
    setIsError(false);
    e.preventDefault();

    // const submitButton = document.getElementById('enter-button');

    // if (submitButton && submitButton.disabled) {
    //   // Button is disabled, do nothing
    //   return;
    // }

    // if user NOT Select a Category / Title
    if ((showTitle && selectedOption) === 'show') {
      setShowNoteTitle(true);
      setIsError(false);
      return;
    }

    console.log('clicked');

    // Check if inputText and inputExcerpt are empty
    if ((!inputText && showTitle) || (!inputExcerpt && showExcerpt)) {
      setShowNote(true);
      setIsError(false);
      setShowNoteTitle(false);
      return;
    }
    
    // Set loading state to true when submitting
    setIsLoading(true);

    // Add a limitation to the Title / Excerpt input
    const ExcerptLimit = 450;
    const TitleLimit   = 30;
    if ((wordCount(inputText) > TitleLimit) || (wordCount(inputExcerpt) > ExcerptLimit)) {
      // alert(`Your input exceeds the ${wordLimit}-word limit. Please shorten your input.`);
      showPopup("Oops! Your input is a bit too long. Could you please shorten it a little?");
      setIsLoading(false);
      return;
    }

  // prompt for TITLE
  // Define prompts based on showTitle and showExcerpt states
  let prompt = [];
  if (showTitle) {
    prompt = [
      // {"role": "system",    "content": "You are a helpful assistant that identifies and formats figurative language and literary devices in text using HTML tags: <ul><li> for list and <b> for main definitions"},
      // {"role": "user",      "content": "What are the most common figurative languages used in this " + selectedOption  + " name " + inputText},
      // {"role": "assistant", "content": `Here's an example of <b>bold text</b> and a line break:<br>This is on a new line`},

      // // // A case
      // {"role": "system",    "content": "You are a helpful assistant that identifies and formats figurative language and literary devices in text."},
      // {"role": "user",      "content": "Please provide the figuratative language (including literarary devices and / or poetic devices) used in " + selectedOption  + " name " + inputText},
      // {"role": "assistant", "content": `Use HTML table tags. Exclude word sure`},

      // B case
      {"role": "system",    "content": "You are a helpful assistant that identifies and formats figurative language and literary devices in text."},
      {"role": "user",      "content": "Please provide the literarary devices used in " + selectedOption  + " name " + inputText},
      {"role": "assistant", "content": `Use HTML table tags. Keep it concise. Do not output markdown. Exclude word 'sure'`},

      // C case
      // {"role": "system",    "content": "You are a helpful assistant that identifies and formats figurative language and literary devices in text."},
      // {"role": "user",      "content": "Please provide the figuratative language (including literarary devices and / or poetic devices) used in " + selectedOption  + " name " + inputText},
      // {"role": "assistant", "content": `Use HTML table tags. Keep it concise and under 100 words. Exclude words sure, certainly`},
    ];
  } else if (showExcerpt) {
    prompt = [
      // {"role": "system",    "content": "You are a helpful assistant that identifies and formats figurative language and literary devices in text using HTML tags: <ul><li> for list and <b> for main definitions"},
      // {"role": "user",      "content": "Find the figuratative devices / poetic devices used in this poem"},
      // {"role": "user",      "content":  inputExcerpt},
      // {"role": "assistant", "content": `Here's an example of <b>bold text</b> and a line break:<br>This is on a new line`},

      {"role": "system",    "content": "You are a helpful assistant that identifies and formats figurative language and literary devices in text."},
      {"role": "user",      "content": "Find the literarary devices used in this poem "},
      {"role": "user",      "content":  inputExcerpt},
      {"role": "assistant", "content": `Use HTML table tags. Keep it concise. Do not output markdown. Exclude word 'sure' `},
    ];
  }

    // prompt for EXCERPT

    const APIBody = {
      "model":             "gpt-4o", // gpt-3.5-turbo-0125
      "messages":          prompt,
      "temperature":       0.8,
      "max_tokens":        2000,
      // "frequency_penalty": 0.1,
      // "presence_penalty":  0.9,
      // top_p: 0.9,
      // top_k: 50,
      // frequency_penalty: 0.5,
      // presence_penalty: 0.3,
      // best_of: 1, 
    }

    try {
      const response = await fetch(apiUrl, {
        method: "POST",
        headers: {
          "Authorization": `Bearer ${apiKey}`,
          "Content-Type": "application/json",
        },
        body: JSON.stringify(APIBody)
      });
    
      // Check if the request was cancelled
      if (requestCancelled) {
        return;
      }
    
      if (!response.ok) {
        throw new Error(`Request failed with status ${response.status}`);
      }
    
      const responseData = await response.json();
      if (!responseData || !responseData.choices || responseData.choices.length === 0) {
        throw new Error("Invalid response data");
      }

      console.log(responseData);
      
      const result = responseData.choices[0].message.content.trim();

      // Paste result to place
      if (showTitle) { setOutputText(convertLinefeedsToHTML(result)) }
      else if (showExcerpt) { setOutputExcerpt(convertLinefeedsToHTML(result)) }
      
      console.log(result);
    } catch (error) {
      console.error("Error:", error);
      setIsError(true);
    } finally {
      setRequestCancelled(false);
      setIsLoading(false);
      console.log(outputText);
    }
  };

  const handleClear = () => {
    if (showTitle) {
      if (inputText || outputText) {
        setInputText('');
        setOutputText('');
        setIsCiteTitle(false);
        setQuoteTitle('');
        showPopup('Text cleared');
      } else {
        showPopup("Nothing to clear.");
      }
    }
  else if (showExcerpt) {
    if (inputExcerpt || outputExcerpt) {
        setInputExcerpt('');
        setOutputExcerpt('');
        setIsCiteExcerpt(false);
        setQuoteExcerpt('');
        showPopup('Text cleared');
      } else {
        showPopup("Nothing to clear.");
      }
    }
  }

  const handleDownload = () => {
    // Save the current text in PDF 
    if (showTitle) {
      if (!outputText) {
        showPopup("No text to save.");
        return;
      }
      const randomNumber = Math.floor(Math.random() * 11);
      const fileName = `litdevices-com_${randomNumber}.pdf`;
      const elementHTML = document.querySelector("#ai-literary-device-finder").cloneNode(true);
      // Remove elements with the class 'exclude'
      const elementsToExclude = elementHTML.querySelectorAll('#title');
      elementsToExclude.forEach(element => element.remove());
      const elementsToExclude2 = elementHTML.querySelectorAll('#share');
      elementsToExclude2.forEach(element => element.remove());
      const elementsToExclude4 = elementHTML.querySelectorAll('.fa-solid');
      elementsToExclude4.forEach(element => element.remove()); 

      const pdf = new jsPDF({
        orientation: 'portrait'
      });
      
      pdf.html(elementHTML, {
          callback: function(doc) {
              // Save the PDF
              doc.save(fileName);
          },
          margin: [10, 10, 10, 10],
          html2canvas: {
            backgroundColor: '#D4F1F4',
        },
          x: 0,
          y: 0,
          width: 190, //target width in the PDF document
          windowWidth: 1424 //window width in CSS pixels

      });
      showPopup("Save action completed");
    }

    else if (showExcerpt) {
      if (!outputExcerpt) {
        showPopup("No text to save.");
        return;
      }
      const randomNumber = Math.floor(Math.random() * 11);
      const fileName = `litdevices-com_${randomNumber}.pdf`;
      const elementHTML = document.querySelector("#ai-literary-device-finder").cloneNode(true);
      // Remove elements with the class 'exclude'
      const elementsToExclude = elementHTML.querySelectorAll('#excerpt');
      elementsToExclude.forEach(element => element.remove());
      const elementsToExclude2 = elementHTML.querySelectorAll('#share');
      elementsToExclude2.forEach(element => element.remove());
      const elementsToExclude3 = elementHTML.querySelectorAll('#title-excerpt');
      elementsToExclude3.forEach(element => element.remove());
      const elementsToExclude4 = elementHTML.querySelectorAll('.fa-solid');
      elementsToExclude4.forEach(element => element.remove()); 

      const pdf = new jsPDF({
        orientation: 'portrait'
      });
      
      pdf.html(elementHTML, {
          callback: function(doc) {
              // Save the PDF
              doc.save(fileName);
          },
          margin: [10, 10, 10, 10],
          html2canvas: {
            backgroundColor: '#D4F1F4',
        },
          x: 0,
          y: 0,
          width: 190, //target width in the PDF document
          windowWidth: 1424 //window width in CSS pixels

      });
    } 
  }

  const handleCopy = () => {
    if (showTitle) {
      if (outputText) {
        navigator.clipboard.writeText(convertAIresponseToText(outputText))
          .then(() => {
            // Show the popup
            showPopup('Text copied to clipboard');
          })
          .catch((err) => {
            console.error("Error copying text:", err);
          });
      } else {
        showPopup("Nothing to copy. Please provide input text");
      }
    }
    else if (showExcerpt) {
      if (outputExcerpt) {
        navigator.clipboard.writeText(convertAIresponseToText(outputExcerpt))
          .then(() => {
            // Show the popup
            showPopup('Text copied to clipboard');
          })
          .catch((err) => {
            console.error("Error copying text:", err);
          });
      } else {
        showPopup("Nothing to copy. Please provide input text");
      }
    }
  }

  const handleCite = () => {
    if (showTitle && outputText) {
      setQuoteTitle('');
      setIsCiteTitle(cite => !cite);
    }
    else if (showExcerpt && outputExcerpt) {
      setQuoteExcerpt('');
      setIsCiteExcerpt(cite => !cite);
    }
  }

  const handleAPA = () => {
    if (showTitle) {
      setQuoteTitle('apa');
    }
    else if (showExcerpt) {
      setQuoteExcerpt('apa');
    }
  }
  const handleHarvard = () => {
    if (showTitle) {
      setQuoteTitle('harvard');
    }
    else if (showExcerpt) {
      setQuoteExcerpt('harvard');
    }
  }
  const handleChicago = () => {
    if (showTitle) {
      setQuoteTitle('chicago');
    }
    else if (showExcerpt) {
      setQuoteExcerpt('chicago');
    }
  }

  return (
    <div id="literary-device-finder" className="container text-center">
      <div className="row justify-content-center">
        <div className="col-12">
          <h2>LitDevices: Explore Literary Devices with AI</h2>
          <TypeAnimation
            sequence={[
              'Find literary, and poetic devices used in books',  
              1000, // Waits 1s
              'Find literary, and poetic devices used in books, poems',
              1000, // Waits 1s
              'Find literary, and poetic devices used in books, poems, movies',  
              1000, // Waits 2s
              'Find literary, and poetic devices used in books, poems, movies, songs',  
              1000, // Waits 2s
              'Find literary, and poetic devices used in books, poems, movies, songs, and more.',  
              () => {
                console.log('Sequence completed');
              },
            ]}
            wrapper="h4"
            cursor={false}
          />
          {/* <h4>Find literary, and poetic devices used in books, poems, movies, songs, and more.</h4> */}
          <h5 id='title-excerpt'>Title &nbsp;&nbsp;
            <label className="switch">
              <input type="checkbox" onClick={titleExcerptToggle} />
              <span className="slider round"></span>
            </label> &nbsp;&nbsp;Excerpt
          </h5>
        </div>
        
        {showTitle && 
          <Title 
            setInputText={setInputText} 
            inputText={inputText} 
            handleSubmit={handleSubmit}
            selectedOption={selectedOption}
            setSelectedOption={setSelectedOption}
            placeholders={placeholders}
          />
        }
        {showExcerpt && 
          <Excerpt 
            setInputExcerpt={setInputExcerpt} 
            inputExcerpt={inputExcerpt} 
            handleSubmit={handleSubmit}
          />
        }
        {showNote      && <h3>Please provide Title or Excerpt</h3>}
        {(showNoteTitle && showTitle) && <h3>Select a category: Book, Movie, Poem, or Song</h3>}
        {isError       && <h3>An error occurred. Please try again.</h3>}
        {isLoading     && <Preloader />}

        <Result 
          outputText={outputText}
          setOutputText={setOutputText} 
          outputExcerpt={outputExcerpt} 
          setOutputExcerpt={setOutputExcerpt}
          showExcerpt={showExcerpt} 
          showTitle={showTitle} 
          isLoading={isLoading}
        />

        { (quoteTitle === 'apa' && showTitle) && 
          <div id='citiation'>
              <p><cite>LitDevices. ({getCurrentFormattedDate1()}). Homepage. Retrieved {getCurrentFormattedDate2()}, from <a href="https://litdevices.com">https://litdevices.com</a></cite></p>
          </div>
        }
        {(quoteExcerpt === 'apa'  && showExcerpt) &&
          <div id='citiation'>
              <p><cite>LitDevices. ({getCurrentFormattedDate1()}). Homepage. Retrieved {getCurrentFormattedDate2()}, from <a href="https://litdevices.com">https://litdevices.com</a></cite></p>
          </div>
        }

        {(quoteTitle === 'harvard' && showTitle) && 
          <div id='citiation'>
              <p><cite>LitDevices, {getCurrentYear()}. Homepage. [online] Available at: <a href="https://litdevices.com">https://litdevices.com</a> [Accessed {getCurrentFormattedDate3()}]</cite></p>
          </div>
        } 
        {(quoteExcerpt === 'harvard' && showExcerpt) &&  
          <div id='citiation'>
              <p><cite>LitDevices, {getCurrentYear()}. Homepage. [online] Available at: <a href="https://litdevices.com">https://litdevices.com</a> [Accessed {getCurrentFormattedDate3()}] </cite></p>
          </div>
        } 

        {(quoteTitle === 'chicago' && showTitle) && 
          <div id='citiation'>
              <p><cite>"Homepage," LitDevices, last modified {getCurrentFormattedDate1()}, <a href="https://litdevices.com">https://litdevices.com</a>.</cite></p>
          </div>
        } 
        {(quoteExcerpt === 'chicago' && showExcerpt) && 
          <div id='citiation'>
              <p><cite>"Homepage," LitDevices, last modified {getCurrentFormattedDate1()}, <a href="https://litdevices.com">https://litdevices.com</a>.</cite></p>
          </div>
        } 

        {(isCiteTitle && showTitle) && 
          <div class="col-12 cite-buttons" id="functional-buttons">
            <button class="btn btn-secondary" onClick={handleAPA}>APA</button>
            <button class="btn btn-secondary" onClick={handleHarvard}>Harvard</button>
            <button class="btn btn-secondary" onClick={handleChicago}>Chicago</button>
          </div>
        }

        {(isCiteExcerpt && showExcerpt) && 
          <div class="col-12 cite-buttons" id="functional-buttons">
            <button class="btn btn-secondary" onClick={handleAPA}>APA</button>
            <button class="btn btn-secondary" onClick={handleHarvard}>Harvard</button>
            <button class="btn btn-secondary" onClick={handleChicago}>Chicago</button>
          </div>
        } 
    
        {/* <div class="col-12" id="functional-buttons">
          <button id="clear" class="btn btn-lg btn-primary" onClick={handleClear}><i class="fas fa-gem"></i> Clear</button>
          <button id="download" class="btn btn-lg btn-secondary" onClick={handleDownload}><i class="fas fa-magic"></i> Download as PDF</button>
          <button id="copy" class="btn btn-lg btn-success" onClick={handleCopy}><i class="fas fa-star"></i> Copy</button>
          <button id="cite" class="btn btn-lg btn-danger" onClick={handleCite}><i class="fab fa-apple"></i> Cite This</button>
        </div> */}

        {/* Functional Buttos */}
        <div class="col-12 col-md-12 col-lg-10 mb-3" id="functional-buttons">
						<div class="row">
							<div class="col-md-3">
								<button type="button" id="clear" class="btn mb-2 mb-md-0 btn-primary btn-block" onClick={handleClear}><span>Clear</span> 
									<div class="icon d-flex align-items-center justify-content-center">
                    <i class="fa-solid fa-trash"></i>
									</div>
								</button>
							</div>
							<div class="col-md-3">
								<button id="download" type="button" class="btn mb-2 mb-md-0 btn-secondary btn-block" onClick={handleDownload}><span>Download as PDF</span> 
									<div class="icon d-flex align-items-center justify-content-center">
                    <i class="fa-solid fa-download"></i> 
									</div>
								</button>
							</div>
							<div class="col-md-3">
								<button id="copy" type="button" class="btn mb-2 mb-md-0 btn-tertiary btn-block" onClick={handleCopy}><span>Copy</span> 
									<div class="icon d-flex align-items-center justify-content-center">
                    <i class="fa-solid fa-copy"></i>
									</div>
								</button>
							</div>
							<div class="col-md-3">
								<button id="cite" type="button" class="btn mb-2 mb-md-0 btn-quarternary btn-block" onClick={handleCite}><span>Cite This</span> 
									<div  class="icon d-flex align-items-center justify-content-center">
                    <i class="fa-solid fa-quote-right"></i>
									</div>
								</button>
							</div>
						</div>
				</div>

        <Share />

      </div>

      {/* Show Popup */}
      <div id="popup" className="popup"></div>
    </div>
  );
}

export default App;
